import React, { Component } from 'react'
import { connect } from 'react-redux'
import { actionCreators } from './store'
//actionCreator 
import { actionCreators as loginActionCreators } from '../../pages/login/store'

import { HeaderWrapper, Logo, Nav, NavItem, NavSearch, Addition, Button, SearchRapper, SearchInfo, SearchInfoTitle, SearchInfoSwitch, SearchInfoItem, SearchInfoList } from './style'

import { CSSTransition } from 'react-transition-group'

import { Link } from 'react-router-dom'

export class Header extends Component {


    getListArea = (show) => {
        //listjs[]listlist
        const newList = this.props.list.toJS()
        const pageList = []
        //keyundifine!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
        if(newList.length){
            for(let i = (this.props.page-1)*10; i < this.props.page * 10; i++){
            pageList.push(
                <SearchInfoItem key={newList[i]}>{newList[i]}</SearchInfoItem>
            )
            
        }

        }

        if (show) {
            return (
                <SearchInfo 
                onMouseEnter={this.props.handleMouseEnter}
                onMouseLeave={this.props.handleOnMouseLeave}
                >
                    <SearchInfoTitle>
                        
                        <SearchInfoSwitch onClick={this.props.handleChangePage.bind(this,this.props.page,this.props.totalPage)}>
                        <i className="fas fa-sync-alt"></i>

                        </SearchInfoSwitch>
                    </SearchInfoTitle>
                    <SearchInfoList>
                        {
                           pageList
                        }

                    </SearchInfoList>
                </SearchInfo>
            )
        } else {
            return null
        }
    }
   
    render() {
        return (
            <HeaderWrapper>
                <Link to='/' > 
                     <Logo></Logo>
                </Link>
               

                <Nav>
                    <NavItem className="left"></NavItem>
                    <NavItem className="left">APP</NavItem>
                    <NavItem className="right"><i className="fas fa-font"></i></NavItem>
                    
                        { this.props.ln?
                        <Link to='#'>
                            <NavItem className="right" onClick={this.props.logout}>
                                </NavItem>
                                    </Link>
                                :
                                    <Link to='/login'>
                                <NavItem className="right">
                                </NavItem>
                        </Link>}
                    


                    <SearchRapper>

                        <CSSTransition
                            timeout={2000}
                            in={this.props.focused}
                            classNames='slide'
                        >
                            <NavSearch
                                placeholder=""
                                className={this.props.focused ? "focused" : ""}
                                onFocus={this.props.handleInputFocus.bind(this,this.props.list)}
                                onBlur={this.props.handleInputBlur}
                            >
                            </NavSearch>
                        </CSSTransition>

                        <i className={this.props.focused ? "fas fa-search focused iconfont" : "fas fa-search iconfont"}></i>


                        {this.getListArea(this.props.focused||this.props.mouseIn)}


                    </SearchRapper>


                </Nav>

                <Addition>
                    <Button className="writting">
                        <i className="fas fa-pen"></i>
                        <Link to='write'> </Link>
                       
                    </Button>
                    <Button className="reg"></Button>
                </Addition>

            </HeaderWrapper>


        )
    }


}

const mapStateToProps = (state) => {
    return {
        focused: state.getIn(['header', 'focused']),
        list:state.getIn(['header','list']),
        // 
        page:state.getIn(['header','page']),

        mouseIn:state.getIn(['header','mouseIn']),

        totalPage:state.getIn(['header','totalPage']),

        ln:state.getIn(['login','login'])
    }
}

const mapDispachToProps = (dispatch) => {
    return {
        handleInputBlur() {
            console.log('blur');
            dispatch(actionCreators.searchBlur())
        },
        handleInputFocus(list) {
            console.log('focus');
            if(!list.size){
                dispatch(actionCreators.getList())//actionCreatorsfun
            }
            dispatch(actionCreators.searchFocus())
        },
        handleMouseEnter(){
            console.log('handle mouse enter')
            dispatch(actionCreators.mouseEnter())
        },
        handleOnMouseLeave(){
            console.log('handle on mouse leave')
            dispatch(actionCreators.mouseLeave())
        },
        handleChangePage(page,totalPage){
            console.log('handel change page','page:',page,'totalpage:',totalPage)
            dispatch(actionCreators.changePage())
            if(page < totalPage){
                dispatch(actionCreators.changePage(page+1))
            }else{
                dispatch(actionCreators.changePage(1))
            }
        },
        logout(){
            //actionCreator
            console.log('logoutlogout');
            dispatch(loginActionCreators.logout())
        }

    }
}

export default connect(mapStateToProps, mapDispachToProps)(Header)



