import React, { Component } from 'react'
import { HomeLeft, HomeRight, HomeWrapper ,BackTop } from './style'
import Topic from './commponent/Topic'
import List from './commponent/List'
import Recommend from './commponent/Recommend'
import Writer from './commponent/Writer'
import { connect } from 'react-redux'
import {actionCreators} from './store'

class Home extends Component {

    handleTop(){
        window.scrollTo(0,0)
    }


    render() {
        return (
            <HomeWrapper>

                <HomeLeft>
                    <img className="banner-img" src="https://cdn.pixabay.com/photo/2019/04/30/12/34/smart-4168483_960_720.jpg" alt=''></img>
                    <Topic/>
                    <List/>
                </HomeLeft>

                <HomeRight>
                    <Recommend/>
                    <Writer/>
                </HomeRight>

                <BackTop onClick={this.handleTop}></BackTop>

            </HomeWrapper>
            

        )
    }

    componentDidMount(){
        this.props.changeHomeData()
    }

}

const mapDispatch = (dispatch) => (
    {
        changeHomeData(){
            dispatch(actionCreators.getHomeInfo())
           //actionCreatorsactionCreatorsstroe
        }
    }
)

export default connect(null,mapDispatch)(Home)




