import {constants} from "./index"
import { fromJS } from "immutable"

const defaultState = fromJS({
    login:false
})

const reducer = (state = defaultState,action ) => {

    if(action.type === constants.CHANGE_LOGIN){
        console.log('reducer  action.loginState:',action.loginState);
        return state.merge(
            {
                login:action.loginState
            }
        )
    }

    if(action.type === constants.LOGOUT){
        console.log('lonin reducer  logout  action.value = ',action.value);
        return state.merge({
            login:action.value
        })
    }

    return state
}

export default reducer


