import styled from "styled-components";

export const HomeWrapper = styled.div`
    width: 960px;
    margin: 0 auto;
    overflow: hidden;
`

export const HomeLeft = styled.div`
    margin-left: 15px;
    padding-top: 30px;
    width: 625px;
    float: left;
    /* background: green; */
    .banner-img{
         width: 625px;
         height: 270px;
    }
`

export const HomeRight = styled.div`
    width: 240px;
    float: right;
    /* background: yellow; */

`

export const TopicWrapper = styled.div`
    padding: 20px 0 10px 0;
    overflow: hidden;
    border-bottom: 1px solid #dcdcdc;
`
export const TopicItem = styled.div`
    float: left;

    height: 32px;
    line-height: 32px;
    font-size: 14px;
    border-radius: 4px;
    color: #000;
    border: 1px solid #dcdcdc;
    padding-right: 10px;
    margin-right: 10px;
    img{
        width: 32px;
        height: 32px;
        display: block;
        float: left;
    }
`

export const ListItem =  styled.div`
    padding: 20px 0;
    border-bottom: 1px solid #dcdcdc;
    float: right;
    img{
        display: block;
        width: 125px;
        height: 100px;
        float: right;
        border-radius: 10px;
    }
`
export const ListInfo = styled.div`
    width: 500px;
    float: left;
    h3{
        line-height: 27px;
        font-size: 18px;
        font-weight: bold;
        color: #333;
    }
    p{
        font-size: 13px;
        line-height: 18px;
        color: #999 ;
    }

`


export const RecommendWrapper = styled.div`
    margin: 30px 0;
    width: 280px;


`
export const RecommendItem = styled.div`
    width: 280px;
    height: 50px;
    
    background:url(${(props)=>props.imgUrl});
    background-size: contain;
`

 
export const WriterWrapper = styled.div`
    width: 278px;
    border: 1px solid #dcdcdc;
    border-radius: 3px;
    height: 300px;
    line-height: 300px;
    text-align: center;
`


export const LoadMore = styled.div`
    clear: both;
    width: 100%;
    height: 40px;
    line-height: 40px;
    background: #a5a5a5;
    text-align: center;
    border-radius: 20px;
    color: #fff;
    margin: 30px 0;
    cursor: pointer;
`

export const BackTop = styled.div`
    position: fixed;
    right: 100px;
    bottom: 100px;
    width: 60px;height: 60px;
    line-height: 60px;
    text-align: 60px;
    border: 1px solid #ccc;
    font-size: 14px;

`


