import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Button, Input, LoginBox, LoginWrapper } from './style'
import { actionCreators } from './store'
import { Redirect } from 'react-router-dom'

class Login extends Component {
    render() {

        if (!this.props.lns) {
            return (
                <LoginWrapper>
                    <LoginBox>
                        <Input placeholder='' ref={(input) => { this.account = input }}></Input>
                        <Input placeholder='' type='password' ref={(input) => { this.password = input }}></Input>
                        <Button onClick={()=>this.props.handleLogin(this.account, this.password)}></Button>
                         innerRef

                    </LoginBox>
                </LoginWrapper>
            )
        }else{
            return <Redirect to='/home'></Redirect>
            
        }


    }
}

const mapState = (state) => ({
    lns: state.get('login').get('login')
})

const mapDispatch = (dispatch) => ({
    handleLogin(account, password) {
        console.log('account:', account.value);
        console.log('password:', password.value);
        dispatch(actionCreators.login(account.value, password.value))
    }
})

export default connect(mapState, mapDispatch)(Login)


