import axios from "axios"
import { fromJS } from "immutable"
import * as constants from './constants'
//actionCreator
const changeHomeDatas = (result) => ({
    type: constants.CHANGE_HOME_DATAS,
    topicList: result.topicList,
    articleList: result.articleList,
    recommendList: result.recommendList
})

export const getHomeInfo = () => {
    return (dispatch) => {
        axios.get('/api/home.json').then(
            (res) => {
                const result = res.data.data
                console.log('get home info:home json',result)
                // actionCreatoraction
                dispatch(changeHomeDatas(result))
            }
        )
    }
}


const addHomeList = (list,nextPage) => ({
    type:constants.ADD_ARTICLE_LIST,
    list:fromJS(list),
    nextPage
})

export const getMoreLists = (page) => {
    return (dispatch) => {
       axios.get('/api/homeList.json?page='+page).then(
           (res)=>{
               const result = res.data.data
               dispatch(addHomeList(result,page+1)) 
           }
       )
    }
}




