import {constants} from "./index"
import axios from "axios"
import { fromJS } from "immutable"


const changeLogin = (result) => ({
    type:constants.CHANGE_LOGIN,
    loginState:fromJS(result)
})


export const login = (accont,password) => {
    return (dispatch) => {
        axios.get('/api/login.json?account='+accont+'&password='+password).then(
            (res)=>{
                
                const result = res.data.data
                console.log( 'login  actioncreator' ,result);

                if(result){
                    dispatch(changeLogin(result))
                }
                
            }
        )
    }
}

export const logout = () => ({
    type:constants.LOGOUT,
    value:false
})


