import styled from "styled-components";
import logoPic from "../../static/nav-logo.png"

export const HeaderWrapper = styled.div`
    position: relative;
    height: 56px;
    border-bottom: 1px solid #f0f0f0;
`

export const Logo = styled.div`
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 100px;
    height: 56px;
    background: url(${logoPic});
    background-size: contain;
`

export const Nav = styled.div`
    width: 660px;
    padding-right:70px;
    box-sizing: border-box;
    height: 100%;
    margin: 0 auto;

`

export const NavItem = styled.div`
    line-height: 56px;
    padding: 0 15px;
    font-size: 17px;
    color: #333;

    &.left{
        float: left;
    }
    &.right{
        float: right;
        color: #969696;
    }
    &.active{
        color: #ea5f5a;
    }

`


export const NavSearch = styled.input`
    width: 160px;
    height: 38px;
    margin-left: 20px;
    border: none;
    border-radius: 19px;
    background: #eee;
    margin-top: 9px;
    padding: 0 35px 0 20px;
    box-sizing: border-box;
    outline: none;
    color: #777;
    &::placeholder{
        color: #999;
    }
    &.focused{
        width: 200px;
        
    }

`

export const SearchInfo = styled.div`
    position: absolute;
    left: 0;
    top: 56px;
    width: 240px;
    padding: 0 20px;
    box-shadow: 0 0 8px rgba(0,0,0,.2);
    float: left;

`
export const SearchInfoSwitch = styled.div`
    float: right;
    font-size: 13px;
    transform: rotate(30deg);

`


export const SearchInfoTitle = styled.div`
    margin-top: 20px;
    margin-bottom: 15px;
    line-height: 20px;
    font-size: 14px;
    color: #969696;

`

export const SearchInfoList = styled.div`
    overflow: hidden;
`

export const SearchInfoItem = styled.a`
     display: block;
     float: left;
     font-size: 12px;
     margin-right: 10px;
     margin-bottom: 10px;
    padding: 0 5px;
    line-height: 20px;
    border-radius: 2px;
   
    border: 1px solid #ddd;
    color: #777;
`

export const SearchRapper = styled.div`
    position: relative;
    float: left;
    .iconfont{
        position: absolute;
        right: 5px;
        bottom: 5px;

        width: 30px;
        line-height: 30px;
        text-align: center;
        border-radius: 15px;

        &.focused{
            background:#777;
            color: #fff;
        }

    }

    .slide-enter{
        transition: all 2s ease-out;
    }
    .slide-enter-active{
        width: 200px;
    }
    .slide-exit{
        transition: all 2s ease-out;
    }
    .slide-exit-active{
        width: 160px;
    }

`

export const Addition = styled.div`
    position: absolute;
    right: 0;
    top: 0;
    height: 56px;
`

export const Button = styled.div`
    float: right;
    line-height:38px ;
    border-radius: 19px;
    margin-top: 9px;
    margin-right:20px;
    padding: 0 20px;
    border: 1px solid #ec6149;

    &.reg{
        color:#ec6149 ;
    }
    &.writting{
        color: #fff;
        background: #ec6149;
    }
`






